<template>
  <div class="row">
    <div v-if="!lessonData.video" class="col-12">
      <img
        class="card-img"
        v-lazy="lessonData.id[0] === 'Q'
        ? `${qingWen}`
        : `https://s3contents.chinesepod.com${lessonData.type === 'extra' ? '/extra/' : '/'}${lessonData.id}/${lessonData.hash_code}/${lessonData.image}`"
        :alt="lessonData.title"
      >
    </div>

    <div class="col-12">
      <vue-plyr ref="plyr" :options="playerOptions">
        <audio>
          <source :src="audioSource" type="audio/mp3"/>
        </audio>
      </vue-plyr>
    </div>
    <div class="col-12">
      <base-button v-if="lessonData.mp3_private" @click.prevent="setAudio('lesson')" :type="isActive === 'lesson' ? 'primary' : 'secondary'">Audio Lesson</base-button>
      <base-button v-if="lessonData.mp3_dialogue" @click.prevent="setAudio('dialogue')" :type="isActive === 'dialogue' ? 'primary' : 'secondary'">Audio Dialogue</base-button>
      <base-button v-if="lessonData.mp3_thefix" @click.prevent="setAudio('review')" :type="isActive === 'review' ? 'primary' : 'secondary'">Audio Review</base-button>
    </div>
  </div>
</template>

<script>

  import ButtonRadioGroup from "../ButtonRadioGroup";

  import Plyr from 'plyr';

  export default {
    name: "LessonAudio",
    components: {ButtonRadioGroup},
    computed: {
      audioSource () {
        return process.env.VUE_APP_ROOT_S3 + this.lessonData.type === 'extra' ? '/extra/' : '/' + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_private']
      },
      player () { return this.$refs.plyr.player }
    },
    props: {
      lessonData: Object
    },
    methods: {
      setAudio (input) {
        switch (input) {
          default:
            this.isActive = input;
            this.player.source = {
              type: 'audio',
              title: 'Audio Lesson',
              sources: [
                {
                  src: process.env.VUE_APP_ROOT_S3 + (this.lessonData.type === 'extra' ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_private'],
                  type: 'audio/mp3',
                },
              ],
            };
            this.player.play();
            break;
          case 'dialogue':
            this.isActive = input;
            this.player.source = {
              type: 'audio',
              title: 'Audio Dialogue',
              sources: [
                {
                  src: process.env.VUE_APP_ROOT_S3 + (this.lessonData.type === 'extra' ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_dialogue'],
                  type: 'audio/mp3',
                },
              ],
            };
            this.player.play();
            break;
          case 'review':
            this.isActive = input;
            this.player.source = {
              type: 'audio',
              title: 'Audio Review',
              sources: [
                {
                  src: process.env.VUE_APP_ROOT_S3 + (this.lessonData.type === 'extra' ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + this.lessonData['mp3_thefix'],
                  type: 'audio/mp3',
                },
              ],
            };
            this.player.play();
            break;
        }
      }
    },
    data () {
      return {
        playerOptions: {
          controls: ['play-large', 'play', 'progress', 'current-time', 'mute', 'volume', 'captions', 'settings', 'pip', 'airplay', 'fullscreen'],
          settings: ['captions', 'quality', 'speed', 'loop'],
          loop: {active: true}
        },
        isActive: 'lesson'
      }
    },
    mounted() {
      this.setAudio('lesson');
    }
  }
</script>

