<template>
  <div>
    <base-button v-if="access === 'premium' || access === 'admin'" class="d-block d-md-none float-right" size="sm" @click="toggleCharSet">{{charSet === 'simplified' ? 'Simp: 汉子' : 'Trad: 漢字'}}</base-button>
    <h2 class="h2">Vocabulary</h2>
    <div class="col-12" v-if="access === 'premium' || access === 'admin'">
      <vue-plyr v-show="showPlayer" ref="plyr">
        <audio></audio>
      </vue-plyr>
      <base-table class=" col-12"
                  :data="vocabData">
        <template slot="columns">
<!--          <th class="px-0 bg-transparent" scope="col">-->
<!--            <span class="text-dark font-weight-700">Add</span>-->
<!--          </th>-->
          <th class="text-center bg-transparent px-2" scope="col">{{ charSet }}</th>
          <th class="d-none d-md-block text-center bg-transparent px-2" scope="col">{{ charSet === 'simplified' ? 'traditional' : 'simplified' }}</th>
          <th class="text-center bg-transparent px-2" scope="col">Pinyin</th>
          <th class="text-center bg-transparent px-2" scope="col">English</th>
          <th class="text-center bg-transparent px-2" scope="col"></th>
        </template>
        <template slot-scope="{row}">
<!--          <td class="px-0 align-middle"><base-checkbox :name="row[charSet.slice(0,1)]" v-model="row['selected']"/></td>-->
          <td class="text-center align-middle vocabulary px-1 px-lg-5 character" v-html="row[charSet.slice(0,1)]"></td>
          <td class="d-none d-md-block align-middle text-center vocabulary px-1 px-lg-5 character" v-html="row[charSet.slice(0,1) === 's' ? 't' : 's' ]"></td>
          <td class="text-center align-middle vocabulary px-1 px-lg-5 " v-html="row.p"></td>
          <td class="text-center align-middle vocabulary px-1 px-lg-5 " v-html="row.en"></td>
          <td class="px-0 align-middle"><base-button size="sm" type="secondary" @click.prevent="playVocab(row.audio)" class="btn-icon-only"><i class='fas fa-volume-up'></i></base-button></td>
        </template>
      </base-table>
    </div>
    <div v-else>
      <upgrade/>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import BaseTable from "../BaseTable";
  import Upgrade from "./Upgrade";

  export default {
    name: "Vocab",
    components: {Upgrade, BaseTable},
    props: {
      lessonId: String,
      lessonData: Object,
      vocabData: Array,
      extra: Boolean
    },
    computed: {
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      charSet() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.charSet : 'simplified'
      },
      player () { return this.$refs.plyr.player }
    },
    data () {
      return {
        selectedVocab: {},
        showPlayer: false
      }
    },
    methods: {
      playVocab(audio) {
        this.showPlayer = true;
        this.player.source = {
          type: 'audio',
          title: 'Vocabulary',
          sources: [
            {
              src: process.env.VUE_APP_ROOT_S3 + (this.extra ? '/extra/' : '/') + this.lessonData.id + '/' + this.lessonData.hash_code + '/' + audio,
              type: 'audio/mp3',
            },
          ],
        };
        this.player.play();
      },
      async toggleCharSet () {
        await this.$store.dispatch('changeCharSet', this.charSet === 'simplified' ? 'traditional' : 'simplified')
      }
    }
  }
</script>

<style scoped>
.vocabulary {
  word-break: break-word;
  flex-wrap: wrap !important;
  white-space: normal !important;
  /*padding: 0 !important;*/
}
.character {
  font-size: 28px;
}
</style>
