<template>
  <div>
    <h2 class="h2">Downloads</h2>
    <div v-if="access === 'premium' || access === 'admin'">
      <div v-if="downloads.type && downloads.type !== 'free'" class="row is-justify-space-between">
        <div v-if="downloads.downloads.lesson" class="col-sm-6 col-md-3 col-xl-2">
          <card class="bg-gradient- border-0 text-center bg-gray"
                header-classes="bg-transparent"
                footer-classes="bg-transparent">
            <h4 slot="header" class="text-uppercase ls-1 text-white py-3 mb-0">Audio Lesson</h4>
            <a :href="downloads.downloads.lesson" target="_blank"  :download="`${lessonId}-lesson.mp3`">
              <base-button type="light" size="lg"><i class="far fa-file-audio"></i></base-button>
            </a>
          </card>
        </div>
        <div v-if="downloads.downloads.dialogue" class="col-sm-6 col-md-3 col-xl-2">
          <card class="bg-gradient- border-0 text-center bg-gray"
                header-classes="bg-transparent"
                footer-classes="bg-transparent">
            <h4 slot="header" class="text-uppercase ls-1 text-white py-3 mb-0">Audio Dialogue</h4>
            <a :href="downloads.downloads.dialogue" target="_blank"  :download="`${lessonId}-dialogue.mp3`">
              <base-button type="light" size="lg"><i class="far fa-file-audio"></i></base-button>
            </a>
          </card>
        </div>
        <div v-if="downloads.downloads.review" class="col-sm-6 col-md-3 col-xl-2">
          <card class="bg-gradient- border-0 text-center bg-gray"
                header-classes="bg-transparent"
                footer-classes="bg-transparent">
            <h4 slot="header" class="text-uppercase ls-1 text-white py-3 mb-0">Audio Review</h4>
            <a :href="downloads.downloads.review" target="_blank"  :download="`${lessonId}-review.mp3`">
              <base-button type="light" size="lg"><i class="far fa-file-audio"></i></base-button>
            </a>
          </card>
        </div>
        <div v-if="downloads.downloads.pdf1" class="col-sm-6 col-md-3 col-xl-2">
          <card class="bg-gradient- border-0 text-center bg-gray"
                header-classes="bg-transparent"
                footer-classes="bg-transparent">
            <h4 slot="header" class="text-uppercase ls-1 text-white py-3 mb-0">Notes (simp.)</h4>
            <a :href="downloads.downloads.pdf1" target="_blank"  :download="`${lessonId}.pdf`">
              <base-button type="light" size="lg"><i class="far fa-file-pdf"></i></base-button>
            </a>
          </card>
        </div>
        <div v-if="downloads.downloads.pdf2"  class="col-sm-6 col-md-3 col-xl-2">
          <card class="bg-gradient- border-0 text-center bg-gray"
                header-classes="bg-transparent"
                footer-classes="bg-transparent">
            <h4 slot="header" class="text-uppercase ls-1 text-white py-3 mb-0">Notes (trad.)</h4>
            <a :href="downloads.downloads.pdf2" target="_blank" :download="`${lessonId}.pdf`">
              <base-button type="light" size="lg"><i class="far fa-file-pdf"></i></base-button>
            </a>
          </card>
        </div>
      </div>
    </div>
    <div v-else>
      <upgrade/>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'

  import Upgrade from "./Upgrade";

  export default {
    name: "Downloads",
    components: {Upgrade},
    props: {
      lessonId: String
    },
    computed: {
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      charSet() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.charSet : 'simplified'
      },
    },
    data () {
      return {
        downloads: {}
      }
    },
    mounted() {
      this.getDownloads();
    },
    methods: {
      async getDownloads () {
        await axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/lessons/get-downloads?lessonId=${this.lessonId}`)
          .then(({data}) => this.downloads = data)
          .catch(() => {})
      }
    }
  }
</script>

<style scoped>
  i {
    font-size: 40px;
  }
</style>
