<template>
  <span>
    <el-popover
      v-if="typeof character === 'object'"
      placement="right"
      width="250"
      trigger="click">
      <p class="character" :id="`character-${sentenceIndex}-${characterIndex}`">{{ character['s'] }}{{ character['s'] === character['t'] ? '' : ` / ${character['t']}` }}</p>
      <p class="pinyin">{{ pinyin }}</p>
      <p class="english">{{ decodeURI(character['en']) }}</p>
      <span class="transcript align-middle" slot="reference">{{ typeof character === 'object' ? charOutput : character }}</span>
    </el-popover>
    <span v-else >{{character}}</span>
  </span>
</template>

<script>
  import toneConvert from 'pinyin-tone-convert'
  import HanziWriter from 'hanzi-writer'

  export default {
    name: "CharacterItem",
    props: {
      character: {
        type: Object | String
      },
      type: {
        type: String,
        default: 'simplified'
      },
      popup: {
        type: Boolean,
        default: false
      },
      characterIndex: Number,
      sentenceIndex: Number
    },
    computed: {
      charOutput () {
        switch (this.type) {
          case 'simplified':
            return this.character['s'];
          case 'traditional':
            return this.character['t'];
          case 'pinyin':
            return ' ' + toneConvert(this.character['p']);
          default:
            return this.character['s'];
        }
      },
      pinyin () {
        return typeof this.character === 'object' ? toneConvert(this.character['p']).toString() : '';
      }
    },
  }
</script>

